* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Hanken Grotesk', sans-serif;
}

button {
    border: none;
    padding: 15px 30px;
}

h1, h3 {
    font-family: 'Hammersmith One', sans-serif;
}



/* Hero Section */

nav {
    position: absolute;
    width: 100%;

    display: flex;
    list-style-type: none;
    justify-content: space-evenly;
    padding: 1.5rem 25rem;

    z-index: 1;
}

nav li {
    color: white;
    font-size: 1.125rem;
    cursor: pointer;
}

.hero {
    position: relative;

    /* Animation */
    transition: opacity 0.25s;
    opacity: 1;
}

.hero.fade-out {
    opacity: 0;
}

.hero-bg {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.hero-bg::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3); /* Replace this with the color and opacity you desire */
}


.hero-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    text-align: center;
}

.hero-content {
    color: white;
}

.hero-h1 {
    font-family: 'Hammersmith One', sans-serif;
    font-size: 6rem;
    font-weight: 400;
    letter-spacing: 8px;
    text-transform: uppercase;
    white-space: nowrap;
}

.hero-h2 {
    font-size: 3rem;
}

/* Body */

.body {
    background-color: #F5F5EF;

    /* Animation */
    transition: opacity 0.25s;
    opacity: 1;
}

.body.fade-out {
    opacity: 0;
}

.body-content {
    display: flex;
    padding: 150px;
}

.body-header {
    font-size: 3rem;
    font-weight: 400;
    line-height: 1;
    margin-bottom: 30px;
}

.body-p {
    width: 80%;
    margin-bottom: 30px;
}

.body-btn {
    background-color: black;
    color: white;
}


.body-imgs-container {
    display: flex;
    gap: 30px;
}

.body-imgs-container img {
    width: 200px;
    height: 410px;
    object-fit: cover;
}

/* Feature */

.ftr {
    background-color: black;
    color: white;

    /* Animation */
    transition: opacity 0.25s;
    opacity: 1;
}

.ftr.fade-out {
    opacity: 0;
}

.ftr-content {
    padding: 150px;
    text-align: center;
}

.ftr-header {
    font-size: 2.5rem;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 80px;
}

.ftr-cards {
    display: flex;
    justify-content: space-evenly;
    gap: 30px
}

.card-icon {
    width: 60px;
    margin-bottom: 40px;
}

.card-header {
    font-weight: 700;
    text-transform: uppercase;
    width: 180px;
    margin: 0 auto 40px auto;
}

.card-p {
    width: 180px;
    text-align: center;
}

/* CTA */

.cta {
    background-color: #F5F5EF;
    display: flex;
    gap: 50px;
    align-items: center;
    padding: 100px;

    /* Animation */
    transition: opacity 0.25s;
    opacity: 1;
}

.cta.fade-out {
    opacity: 0;
}

.cta-left {
    width: 50%;
    object-fit: cover;
    overflow: hidden;
    
}

.cta-header {
    font-size: 2.5rem;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 40px;
}

.cta-p {
    font-size: 1.2rem;
    width: 80%;
    margin-bottom: 40px;
}

.cta-btn {
    background-color: #715128;
    color: white;
}
